.chatitem_container{
    padding: 10px;
    display: flex;
    align-items: center;


}
.chatitem_avatar{
    width: 48px;
    height: 48px;
    position: relative;
}

.avatar_image{
    width: 48px;
    height: 48px;
    border-radius: 24px;
}

.status_light{
    height: 9px;
    width: 9px;
    background-color: #33CC5E;
    border-radius: 9px;
    border: 2px solid #fff;
    position: absolute;
    bottom: 0;
    right: 0;
}
.chatitem_content{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    flex: 1;
}

.chat_userName{
    font-size: 16px;
    line-height: 23px;
    font-weight: bold;
    margin-bottom: 4px;
    min-width: fit-content;
    overflow-x: hidden;
    white-space:nowrap;
}

.chat_userName_ctn{
    display: flex;
    /* justify-content: center; */
    align-items: center;

}
.grouptag{
    height: 16px;
    width: 32px;
    background-color:#4080FF;
    color: #fff;
    font-size: 8px;
    font-weight: 500    ;
    justify-self:center;
    border-radius: 4.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 4px;
}

.chat_message{
    font-size: 14px;
    width: 100%;
    white-space:nowrap;

}
.chatitem_right_ctn{
    width: fit-content;
    height: 48px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.chat_item_time{
    color: #808999;
    margin-bottom: 12px;
}
.isSeen_icon{
    height: 10px;
    width: 10px;
    border-radius: 16px;
    background-color: #FF7F08;
}
