.menu_container{
    height: 100%;
    width: 100%;
    background-color: #115DD3;
    border-top-right-radius: 35px;
    padding: 35px 0px 30px 0px;
    display: flex;
    flex-direction: column;
    transition: 0.2s;
    overflow-x: hidden;
}

.menu_container.active{
    width: 220px;
}

.menu_header{
    flex: 1;
}
.menu_footer{
    margin-bottom: 50px;
}
#largeMenu{
    display: none;
}
